import React, { Component } from "react";
import PropTypes from 'prop-types';
import { BigNumber } from 'bignumber.js'; // not web3.utils.BN
import NumericInput from 'react-numeric-input';

import sig2vrs from "utils/sig2vrs";
import api from "utils/api";
import getParameterByName from "utils/getParameterByName";
import hexToEvenLengthHex from "utils/hexToEvenLengthHex";

// import styles from "../styles/DelegatedTransferForm.module.css";

class DelegatedTransferForm extends Component {
  state = {
    delegate: "0xe8A098f3643Fd62C05A659F6b75f48562003b900", // "0x0000000000000000000000000000000000000000" if no specific delegate
    recipient: "",
    amount: 100, // SELF
    fee: new BigNumber(0),
    orderId: null,
    isDisabled: false,
    delegatedTransfer_api_url: "https://test.selftoken.co/v1/delegatedTransfer",
    local_delegatedTransfer_api_url: "http://localhost:8080/v1/delegatedTransfer",
  };

  componentDidMount = () => {
    const { web3 } = this.props;

    const query_to = getParameterByName("to");
    const query_self = getParameterByName("self");
    // const query_amount = getParameterByName("amount");

    if (!!query_to) {
      if (web3.utils.isAddress(query_to)) {
        console.log("DelegatedTransferForm: query_to correctly parsed");
        this.setState({
          recipient: query_to,
        })
      } else {
        console.warn("DelegatedTransferForm: query_to is provided but not an address");
      }
    }

    if (!!query_self) {
      const amount = parseFloat(query_self);
      if (!isNaN(amount)) {
        console.log("DelegatedTransferForm: self amount parsed, get", amount);
        this.setState({
          amount: amount,
        });
      } else {
        console.warn("DelegatedTransferForm: query_self is provided but parseFloat returns NaN.");
      }
    }

    // now only work on localhost
    if (window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1") {
      this.setState({
        isDisabled: false,
        delegatedTransfer_api_url: this.state.local_delegatedTransfer_api_url,
      });
    } else {
      this.setState({
        isDisabled: false,
      });
    }
  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  handleRecipientChange = (event) => {
    this.setState({
      recipient: event.target.value.trim(),
    });
  }

  handleOrderIdChange = (value) => {
    this.setState({
      orderId: value,
    });
  }

  handleDelegatedTransferFormSubmit = async (event) => {
    event.preventDefault();

    const {
      web3,
      accounts,
      delegatedTransferOperator,
      networkId,
      strings,
      selfBalanceBN,
      callback
    } = this.props;

    const {
      recipient,
      amount, // number
      delegate,
      fee,
      isDisabled,
      delegatedTransfer_api_url,
      orderId,
    } = this.state;

    if (isDisabled) {
      return;
    }

    if (accounts.length === 0) {
      alert(strings.msg_no_account + "\n" + strings.msg_please_unlock);
      return;
    }

    // check address and amount
    console.log("recipient", recipient);
    console.log("amount", amount, typeof amount);
    if (!web3.utils.isAddress(recipient)) {
      alert(strings.msg_invalid_recipient_address);
      return;
    }

    let amountBN = new BigNumber(amount);
    let amountWeiBN = amountBN.times(1e18);

    const _operator = delegatedTransferOperator._address;
    const _to = recipient;
    // TODO: use server's wallet address
    const _delegate = delegate;
    const _value = amountWeiBN;
    const _fee = fee;
    const _nonce = Math.floor(Date.now() / 1000);
    const _userDataNumber = orderId;
    const _userData = (typeof _userDataNumber === "number") ?
      hexToEvenLengthHex(web3.utils.numberToHex(_userDataNumber)) :
      "0x";
    console.log("_operator", _operator);
    console.log("_to", _to);
    console.log("_delegate", _delegate);
    console.log("_value.toString()", _value.toString());
    console.log("_fee.toString()", _fee.toString());
    console.log("_nonce.toString()", _nonce.toString());
    console.log("_userData", _userData);

    if (amountWeiBN.plus(fee).isGreaterThan(selfBalanceBN)) {
      alert(strings.msg_not_enough_self);
      return;
    }

    // delegationHash is a 32-byte ("0x" + 64 chars) hex string.
    const delegationHash = web3.utils.soliditySha3(
      {t: "address", v: _operator},
      {t: "address", v: _to},
      {t: "address", v: _delegate},
      {t: "uint256", v: _value.toString()},
      {t: "uint256", v: _fee.toString()},
      {t: "uint256", v: _nonce.toString()},
      {t: "bytes", v: _userData},
    );
    console.log("delegationHash", delegationHash);

    // delegationSignature is a 65-byte ("0x" + 130 chars) hex string.
    const _signature = await web3.eth.sign(delegationHash, accounts[0]);
    const _vrs = sig2vrs(_signature);
    const _int_v = parseInt(_vrs.v);

    if (_int_v !== 27 && _int_v !== 28) {
      throw new Error("_vrs.v not 27 or 28");
    }

    const version = 1;

    const tx_data = delegatedTransferOperator.methods.transferPreSigned(
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      // _signature
      _vrs.r,
      _vrs.s,
      _vrs.v
    // );
    ).encodeABI();
    console.log("tx_data", tx_data);

    const post_data = {
      networkId,
      version,
      delegatedTransferOperatorAddress: delegatedTransferOperator._address,
      tx_data,
    }

    api(delegatedTransfer_api_url, post_data, (order) => {
      // if transaction is broadcasted
      // `data` is `order`
      console.log("order", order);
      alert(
        strings.msg_transaction_submitted + "\n" +
        strings.Order_ID + ": " + order._id
      );
      callback();
    }, error => {
      // if failed
      alert(
        strings.msg_tx_failed + strings.msg_possible_cause + "\n" +
        "1. " + strings.msg_not_enough_self + "\n" +
        "2. " + strings.msg_error_in_server + "\n" +
        strings.msg_please_contact_us_and_let_us_help_you
      )
      console.error("error", error);
    });
  }

  render() {
    const { web3, strings, selfBalanceBN } = this.props;
    const { amount, fee, recipient } = this.state;

    const amountWeiBN = (new BigNumber(amount)).times(1e18);
    const isTokenNotEnough = amountWeiBN.plus(fee).isGreaterThan(selfBalanceBN);

    const isAddressInvalid = !web3.utils.isAddress(recipient);

    return (
      <form
        onSubmit={this.handleDelegatedTransferFormSubmit}
        id="transfer-form"
        className="transfer-form narrow-container"
      >
        <div className="form-group">
          <label htmlFor="delegatedTransferRecipientInput">
            { strings.Recipient }
          </label>

          <input
            type="text"
            className={
              "address-input address-text" +
              ( isAddressInvalid ? " text-danger" : "" )
            }
            id="delegatedTransferRecipientInput"
            value={this.state.recipient}
            onChange={this.handleRecipientChange}
            placeholder="0x0000000000000000000000000000000000000000"
          />
        </div>

        <div className="form-group">
          <div className="text-center mb-3">
            <label htmlFor="transferAmount">{ strings.Amount } ({ strings.EN_SELF })</label>
            <NumericInput
              id="transferAmount"
              type="number"
              pattern="\d*"
              mobile
              className={
                "numeric-input" + (isTokenNotEnough?" text-danger":"")
              }
              min={0}
              step={10}
              value={this.state.amount}
              onChange={this.handleAmountChange}
            />
          </div>
        </div>
{/*
        <div className="form-group">
          <div className="text-center mb-3">
            <label htmlFor="orderId">Order ID</label>
            <NumericInput
              id="orderId"
              mobile
              className="numeric-input"
              min={0}
              step={1}
              value={this.state.orderId}
              onChange={this.handleOrderIdChange}
            />
          </div>
        </div> */}

        <div className="text-center">
          <button type="submit" className="solid-rectangle-button">
            <span>{ strings.Transfer }</span>
          </button>
        </div>
      </form>
    );
  }
}

DelegatedTransferForm.propTypes = {
  web3: PropTypes.object,
  accounts: PropTypes.array,
  delegatedTransferOperator: PropTypes.object,
  networkId: PropTypes.number,
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
  callback: PropTypes.func,
};

DelegatedTransferForm.defaultProps = {
  web3: {},
  accounts: [],
  delegatedTransferOperator: {},
  networkId: 1,
  strings: {},
  selfBalanceBN: new BigNumber(),
  callback: () => {},
};

export default DelegatedTransferForm;
